import { getAlbumSublist } from '@/request/api/collect';
export default {
  data() {
    return {
      // 用户收藏的专辑(不包括数字专辑)
      collectAlbums: []
    };
  },

  async mounted() {
    if (sessionStorage.getItem("cookie")) {
      // 获取已收藏专辑列表
      const res = await getAlbumSublist(sessionStorage.getItem("cookie")); // console.log('获取已收藏专辑列表', res);

      this.collectAlbums = res.data.data; // console.log('已收藏专辑列表', this.collectAlbums);
    }
  },

  methods: {
    // 去往专辑页
    goDetail(id) {
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1歌单 2专辑
          type: 2
        }
      });
    }

  }
};