// 导入 axios 实例
import service from "..";

// 获取已收藏专辑列表
export function getAlbumSublist(cookie) {
  return service({
    method: 'GET',
    url: `/album/sublist?cookie=${cookie}`
  })
}

// 获取收藏的歌手列表
export function getArtistSublist(cookie, limit) {
  return service({
    method: 'GET',
    url: `/artist/sublist?cookie=${cookie}&limit=${limit}`
  })
}

// 获取收藏的视频列表
export function getMvSublist(cookie) {
  return service({
    method: 'GET',
    url: `/mv/sublist?cookie=${cookie}`
  })
}

// 获取收藏的专栏
export function getTopicSublist(cookie) {
  return service({
    method: 'GET',
    url: `/topic/sublist?cookie=${cookie}`
  })
}